@import url('https://fonts.googleapis.com/css2?family=Grand+Hotel&display=swap');

a{
  color: black !important;
}

.brand-logo,h2{
  font-family: 'Grand Hotel', cursive;
}

.mycard{
  margin-top: 30px;
}

.auth-card{
  padding: 20px;
  text-align: center;
  max-width: 400px;
  margin: 10px auto;
}

.gallery{
  display: grid;
  grid-template-columns: repeat(3,1fr);
  grid-gap: 5px;
}

.item{
  width: 250px;
  height: 250px;
}

.home-card{
  max-width: 500px;
  height: max-content;
  margin: 26px auto;
}

.material-icons:hover{
  cursor: pointer;
}